html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
  
}



nav {
  grid-area: nav;
  background-color: #3d2337;
  min-height: 10vh;
  display: flex;

  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: rgb(221, 221, 221);
  
}


.header-navs {
  justify-content: space-around;
  align-items: center;
}

.cart {
  background: none;
  border: none;
  outline: none;
  
}

.overflowed-store {
  position: absolute;
  right: 0px;
  height: 88vh;
  top: 13vh;
  background-color: #f3f3f3;
  display: flex;

  justify-content: center;
  align-items: center;
  width: 100%;
  transform: translateX(100%);
  transition: 0.1s;
  z-index: 2;

  
}

.store-wrapper {
 
  background-color: white;
  width: 90%;
  height: 90%;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.store-back {
  cursor: pointer;
  background: transparent;
  border: 0;
  fill: rgb(255, 255, 255);

  outline: none;
  align-self: flex-start;
  justify-self: left;
  padding-top: 0.8%;
  transform: translateX(2vh);
  
}

.store-content-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 90%;
  height: 90%;
  border-radius: 10px;
}

.ordered-products {
  width: 60%;
  height: 100%;
  border-radius: 10px;
  border: 2px solid rgba(0, 0, 0, 0.24);
  display: flex;
  flex-wrap: wrap;
  overflow-y: auto;
  align-content: flex-start;
  justify-content: space-evenly;
}

.store-content-container ::-webkit-scrollbar {
  width: 10px;
  border-radius: 10px;
}

.store-content-container ::-webkit-scrollbar-thumb {
  background: #3d233786; 
  border-radius: 10px;
  
}

.store-content-container ::-webkit-scrollbar-thumb:hover {
  background: #3d2337; 

  
}

.product-container {
  width: 8em;
  height: 6em;
  border: 2px solid rgba(0, 0, 0, 0.24);
  border-radius: 10px;
  margin: 1em;
  display: flex; 
  flex-direction: column;

}

.product-photo {
  display: flex;
  height: 5em;
  width: 100%;
  
}

.product-photo img {
  width: 100%;
  height: 5em;
  border-radius: 10px;
  
}

.product-exp {
  width: 100%;
  height: 100%;
  display: flex;
}

.product-exp *{
  height: 1em;
  width: 50%;
  margin: 0px;
}

.ordering {
  
  width: 35%;
  height: 100%;
  border-radius: 10px;
  background-color: white;
  border: 2px solid rgba(0, 0, 0, 0.027)
}

.order-form-container {
  width: 100%;
  height: 30%;
  padding: 1em;
}

.order-form {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  
}



.order-form form  {display: table;  
  height: 100%;
  width: 100%;   
}
.order-form p     {display: table-row; 
  width: 100%;
  height: 20%;
  margin: 10px;
}
.order-form label {display: table-cell;
  height: 20%;
}
.order-form input {display: table-cell;
  width: 100%;
  
}
.order-form button {display: table-cell;

}

.submitted-orders {
  height: 70%;
  width: 100%;
  
}

.carousel-image-small {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgb(95, 95, 95);
  object-fit: cover;
}

.cart-svg{
  fill: rgb(243, 243, 243);
  cursor: pointer;
}

section {
  grid-area: section;
  
  display: flex;
  flex-wrap: wrap;
  background: #02020210
}

.blank {
  background: rgb(66, 43, 90);
  width: 100%;
  height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-products {
  height: 50vh;
  width: 100%;
  padding-top: 2em;
  padding-bottom: 4em;
  
  margin: 3em;
  margin-left: 10em;
  margin-right: 10em;

  background: rgba(255, 255, 255, 0.973);
  border-radius: 10px;
}

.carousel-wrapper {
  width: 100%;
  padding-top: 2em;
  padding-bottom: 4em;
  
  margin: 3em;
  margin-left: 10em;
  margin-right: 10em;

  background: rgba(255, 255, 255, 0.973);
  border-radius: 10px;
  box-shadow: 5px 5px 30px grey;
}

.carousel {
  position: relative;
  height: 600px;
  width: 40%;
  margin: 0 auto;
  
}


.carousel-container {
  background: #f3f3f3;
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 10px;
  border: 2px solid #f3f3f3;
  box-sizing: border-box;
  
}

.carousel-slide {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  height: 100%;
  transition: 0.5s;
  align-items: center;
  justify-content: center;
  box-shadow: 5px 5px 30px rgb(95, 95, 95) inset;

}

.carousel-image-wrapper {
  margin-top: 5%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  
}

.carousel-image {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgb(95, 95, 95);
  object-fit: cover;
}

.carousel-form-box {
  height: 10em;
  position: relative;
  overflow-wrap: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-20%);
  cursor: pointer;
  background: transparent;
  border: 0;
  z-index: 1;
  fill: rgb(39, 39, 39);
  outline: none;
}

.carousel-button--left {
  left: 5px;

}

.carousel-button--right {
  right: 5px;
}

.carousel-button--right svg{
  transform: rotate(180deg);
}

.carousel-nav {
  display: flex;
  justify-content: center;
  padding: 10px 0;

}

.carousel-indicator {
  border: 0;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background: rgba(0, 0, 0, 0.3);
  margin: 0 12px;
  cursor: pointer;
  outline: none;
}

.carousel-indicator:hover {
  background: rgba(0, 0, 0, 0.7)
}

footer {
  grid-area: footer;

}
 
.App{
  display: grid;
  grid-template-rows: repeat(3,auto);
  grid-auto-columns: repeat(12, 1fr);
  grid-template-areas: 
  'nav nav nav nav nav nav nav nav nav nav nav nav' 
  'section section section section section section section section section section section section ' 
  'footer footer footer footer footer footer footer footer footer footer footer footer';
}



